<template>
    <div>
        <el-card class="box-card">
            <div slot="header" class="clearfix card-header">
                <el-breadcrumb separator="/">
                    <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
                    <el-breadcrumb-item>数据中心</el-breadcrumb-item>
                </el-breadcrumb>
            </div>
            <el-tabs v-model="activeName" @tab-click="handleClick">
                <!-- 功能统计 -->
                <el-tab-pane label="功能统计" name="first" style="width: 100%;">
                    <el-form :inline="true" :model="searchForm" label-width="80px">
                        <el-form-item label='显示类型'>
                            <el-select @change=getDate(searchForm.type) v-model='searchForm.type'>
                                <el-option label='日' value='1' key='1'></el-option>
                                <el-option label='月' value='2' key='2'></el-option>
                                <el-option label='年' value='3' key='3'></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item v-show="yearcheck">
                            <el-select v-model='searchForm.year' style="width: 120px">
                                <el-option v-for="item in yearList" :label='item.year' :value='item.year' :key='item.id'></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item v-show="monthcheck">
                            <el-select v-model='searchForm.month' style="width: 120px">
                                <el-option v-for="item in monthList" :label='item.month' :value='item.month' :key='item.id'></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item>
                            <el-button @click='getList()' type='primary' plain>确定</el-button>
                        </el-form-item>
                    </el-form>
                    <!-- 数据统计 -->
                    <div class="charts" style="width: 100%">
                        <div id="myChart" :style="{width:'100%',height:'400px'}"></div>
                    </div>
                    <!-- end数据统计 -->
                </el-tab-pane>
                <!-- end功能统计 -->
                <el-tab-pane label="商品统计" name="second">商品统计</el-tab-pane>
                <el-tab-pane label="销售统计" name="third">销售统计</el-tab-pane>
                <el-tab-pane label="代理商数据" name="fourth">代理商数据</el-tab-pane>
            </el-tabs>
        </el-card>
    </div>
</template>

<script>
import qs from 'qs'
import domain from '../../../static/js/url.js'
export default {
  name: 'App',
  data() {
    return {
      activeName: 'first',
      yearcheck: true, //选择年
      monthcheck: true, //选择月
      nowyear: '', //当前年份
      nowday: '', //当前天数
      shareList: [], //分享数据
      yearList: [], //年份数据
      monthList: [], //月份数据
      datecheck: '1', //选择的是年  还是月  还是 日
      // 搜索条件
      searchForm: {
        type: '1',
        year: '',
        month: ''
      },
      couponSelected: '',
      myChart: '', //折线图
      xAxis: [], //X轴的数据
      getSetOption: {
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: ['今日分享总数', '今日积分兑换总数', '今日签到总数']
        },
        xAxis: [
          {
            type: 'category',
            boundaryGap: false,
            data: []
          }
        ],
        yAxis: {
          type: 'value'
        },
        series: [
          {
            name: '今日分享总数',
            type: 'line',
            color: '#37a2da',
            smooth: true,
            data: []
          },
          {
            name: '今日积分兑换总数',
            type: 'line',
            color: '#67e0e3',
            smooth: true,
            data: []
          },
          {
            name: '今日签到总数',
            type: 'line',
            color: '#ff9f7f',
            smooth: true,
            data: []
          }
        ]
      } //折线图数据
    };
  },
  activated () {
    this.getYearMonth()
  },
  methods: {
    handleClick (tab, event) {
      console.log(tab, event);
    },
    // 功能统计数据列表
    getList () {
      let datecheck = this.datecheck
      let _this = this
      let da = {}
      if (datecheck == 1) {
        (da.time_way = 'day'),
          (da.date_time =
            this.searchForm.year.split('年')[0] +
            '-' +
            this.searchForm.month.split('月')[0] +
            '-' +
            this.nowday);
      } else if (datecheck == 2) {
        (da.time_way = 'month'),
        (da.date_time =
          this.searchForm.year.split('年')[0] +
          '-' +
          this.searchForm.month.split('月')[0] +
          '-' +
          this.nowday)
      } else {
        ( da.time_way = 'year'), (da.date_time = '')
      }
      this.$axios
        .post(
          domain.testUrl + '/admin/report_forms/statistics',
          qs.stringify(da),
          {
            headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
          }
        )
        .then(res => {
          if (res.data.code == '200') {
            this.myChart.setOption({
              xAxis: [
                {
                  type: 'category',
                  boundaryGap: false,
                  data: this.xAxis
                }
              ],
              series: [
                {
                  name: '今日分享总数',
                  type: 'line',
                  color: '#37a2da',
                  smooth: true,
                  data: res.data.data.share
                },
                {
                  name: '今日积分兑换总数',
                  type: 'line',
                  color: '#67e0e3',
                  smooth: true,
                  data: res.data.data.integral
                },
                {
                  name: '今日签到总数',
                  type: 'line',
                  color: '#ff9f7f',
                  smooth: true,
                  data: res.data.data.sign_in
                }
              ]
            })
            this.myChart.setOption.clear()
            this.myChart.setOption(option, false, false)
          } else {
            this.sureLoading = false
            _this.$Notice.warning({ title: res.data.message })
          }
        })
        .catch(function () {
          _this.$Notice.error({ title: '请求失败，请重试' })
        })
    },
    // 切换年月日 的值
    getDate (datecheck) {
      var _this = this
      _this.xAxis.splice(0, _this.xAxis.length)
      this.datecheck = datecheck
      if (datecheck === 1) {
        // 日
        this.yearcheck = true
        this.monthcheck = true
        var date = new Date()
        var year = date.getFullYear()
        var month = date.getMonth() + 1
        var d = new Date(year, month, 0)
        d = d.getDate()
        for (let j = 1; j <= d; j++) {
          this.xAxis.push(j)
        }
      } else if (datecheck === 2) {
        // 月
        this.yearcheck = true
        this.monthcheck = false
        let date = new Date()
        let year = date.getFullYear()
        for (let i = 2018; i < year; i++) {
          this.yearList.push({ id: i, year: i + '月' })
        }
        // 月份横坐标的值
        for (let j = 1; j < 13; j++) {
          _this.xAxis.push(j)
        }
      } else {
        // 年
        this.yearcheck = false
        this.monthcheck = false
        // 年 横坐标的值
        let date = new Date()
        let year = date.getFullYear()
        for (let i = 2018; i <= year; i++) {
          _this.xAxis.push(i)
        }
      }
    },
    // 默认获取当前年月日
    getYearMonth () {
      var date = new Date()
      var year = date.getFullYear()
      var month = date.getMonth() + 1
      this.searchForm.year = year + '年'
      this.searchForm.month = month + '月'
      this.nowday = date.getDate()
      var d = new Date(year, month, 0)
      d = d.getDate()
      this.yearList = [{ id: '1', year: year + '年' }]
      for (let i = 1; i < 13; i++) {
        if (month === i) {
          this.monthList.push({ id: '-1', month: i + '月' })
        } else {
          this.monthList.push({ id: i, month: i + '月' })
        }
      }
      for (let j = 1; j <= d; j++) {
        this.xAxis.push(j)
      }
      this.getList()
      console.log(this.xAxis)
    },
    // 折线图
    drawLineChart () {
      this.myChart = this.$echarts.init(document.getElementById('myChart'))
      this.myChart.setOption(this.getSetOption)
    }
  },
  mounted () {
    this.drawLineChart()
  }
}
</script>

<style>
#main {
  display: block;
  margin: 0 auto;
}

.statistic-table .el-table {
  width: 100%;
}

.statistic-table .el-table .el-table__header {
  width: 100%;
}

.statistic-table .el-table .el-table__body {
  width: 100%;
}

#hf0,
#hf1,
#hf2,
#hf3,
#hf4,
#hf5,
#hf6,
#hf7,
#hf8 {
  display: inline-block;
  font-size: 0;
  color: #fff;
  line-height: 0;
  width: 0;
  height: 0;
}
</style>



