<template>
  <main id="main">
    <div class="main-content allcontent">
      <div class="main-panle">
          <div class="store_title">门店数据</div>
          <ul class="store_data">
            <li class="store_order bluesheepblue">
              <p class="store_order-data bluecolor">新增会员</p>
              <p class="over relative store_little">
                <span class="fl little_bit"></span>
                <span class="store_today-count">今日新增用户</span>
                <span class="fr store-right">201</span>
              </p>
              <p class="over relative store_little">
                <span class="fl little_bit"></span>
                <span class="store_today-count">本月新增用户</span>
                <span class="fr store-right">201</span>
              </p>
              <p class="over relative">
                <span class="fl little_bit"></span>
                <span class="store_today-count">商品订单数</span>
                <span class="fr store-right">201</span>
              </p>
            </li>
            <li class="store_order orange_tint">
              <p class="store_order-data orange_deep">活跃人数</p>
              <p class="over relative store_little">
                <span class="fl little_bit"></span>
                <span class="store_today-count">今日活跃人数</span>
                <span class="fr store-right">201</span>
              </p>
              <p class="over relative store_little">
                <span class="fl little_bit"></span>
                <span class="store_today-count">本月活跃人数</span>
                <span class="fr store-right">201</span>
              </p>
            </li>
            <li class="store_order purple_tint">
              <p class="store_order-data purple_deep">订单数据</p>
              <p class="over relative store_little">
                <span class="fl little_bit"></span>
                <span class="store_today-count">今日订单数</span>
                <span class="fr store-right">201</span>
              </p>
              <p class="over relative store_little">
                <span class="fl little_bit"></span>
                <span class="store_today-count">本月订单数</span>
                <span class="fr store-right">201</span>
              </p>
              <p class="over relative">
                <span class="fl little_bit"></span>
                <span class="store_today-count">其他收入</span>
                <span class="fr store-right">201</span>
              </p>
            </li>
            <li class="store_order red_tint">
              <p class="store_order-data red_deep">销售数据</p>
              <p class="over relative store_little">
                <span class="fl little_bit"></span>
                <span class="store_today-count">今日销售数</span>
                <span class="fr store-right">201</span>
              </p>
              <p class="over relative store_little">
                <span class="fl little_bit"></span>
                <span class="store_today-count">本月销售数</span>
                <span class="fr store-right">201</span>
              </p>
              <p class="over relative">
                <span class="fl little_bit"></span>
                <span class="store_today-count">其他收入</span>
                <span class="fr store-right">201</span>
              </p>
            </li>
          </ul>
      </div>
      <div class="main_table">
          <div class="main-panle mt_10 main-flex order-flex">
            <div class="store_title">订单数据</div>
            <div class="charts">
              <div id="myChart" class="chart" :style="{width:'100%',height:'300px'}"></div>
            </div>
          </div>
          <div class="main-panle mt_10 main-flex order-flex">
            <div class="store_title">销售数据</div>
            <div class="charts">
              <div id="myChart1" class="chart" :style="{width:'100%',height:'300px'}"></div>
            </div>
          </div>
      </div>
    </div>
  </main>
</template>

<script>
export default {
  name: 'chart',
  data () {
    return {
      authour: 'WiseWrong',
      activeName: '1',
      // temp:true,
      Alist: [{
        name: '正在服务技师'
      }, {
        name: '等待服务技师'
      }],
      active: '正在服务技师'
    }
  },
  methods: {
    selected (name) {
      this.active = name
    }
  },
  mounted () {
    let myChart = this.$echarts.init(document.getElementById('myChart'))
    myChart.setOption({
      tooltip: {
        trigger: 'axis'
      },
      legend: {
        data: ['今日订单总数', '服务订单数', '商品订单数']
      },
      xAxis: [
        {
          type: 'category',
          boundaryGap: false,
          data: [
            '05:00',
            '06:15',
            '07:30',
            '08:45',
            '10:00',
            '11:15',
            '12:30',
            '13:45',
            '15:00',
            '16:15',
            '17:30'
          ]
        }
      ],
      yAxis: {},
      series: [
        {
          name: '今日订单总数',
          type: 'line',
          smooth: true,
          data: [120, 132, 101, 134, 90, 230, 210, 220, 182, 191, 234, 290, 330]
        },
        {
          name: '服务订单数',
          type: 'line',
          smooth: true,
          data: [220, 182, 191, 234, 290, 330, 310, 150, 232, 201, 154, 190]
        },
        {
          name: '商品订单数',
          type: 'line',
          smooth: true,
          data: [150, 232, 201, 154, 190, 330, 410, 120, 500, 10, 220, 150, 130]
        }
      ]
    })
    let myChart2 = this.$echarts.init(document.getElementById('myChart1'))
    myChart2.setOption({
      tooltip: {
        trigger: 'axis'
      },
      legend: {
        data: ['今日订单总数', '服务订单数', '商品订单数']
      },
      xAxis: [
        {
          type: 'category',
          boundaryGap: false,
          data: [
            '05:00',
            '06:15',
            '07:30',
            '08:45',
            '10:00',
            '11:15',
            '12:30',
            '13:45',
            '15:00',
            '16:15',
            '17:30'
          ]
        }
      ],
      yAxis: {},
      series: [
        {
          name: '今日订单总数',
          type: 'line',
          smooth: true,
          data: [120, 132, 101, 134, 90, 230, 210, 220, 182, 191, 234, 290, 330]
        },
        {
          name: '服务订单数',
          type: 'line',
          smooth: true,
          data: [220, 182, 191, 234, 290, 330, 310, 150, 232, 201, 154, 190]
        },
        {
          name: '商品订单数',
          type: 'line',
          smooth: true,
          data: [150, 232, 201, 154, 190, 330, 410, 120, 500, 10, 220, 150, 130]
        }
      ]
    })
  }
}
</script>

<style>
  /* .active {
    background: #41B5F0;
    color: white;
  } */
  .s-bt{
    background: #CEDEE6;
    color: #77A2B9;
    line-height: 55px;
    font-size: 14px;
  }
  .active {
    background: #41B5F0;
    color: white;
    font-size: 17px;
    font-size: 14px;
  }
  .nav-text{
    width: 100%;
  }
  .ivu-tabs-tab{
    width: 50%!important;
    background: #CEDEE6;
    color: #77A2B9;
    text-align: center;
    line-height: 30px;
  }
  .ivu-tabs-ink-bar{
    display: none!important;
  }
  .ivu-tabs-tab-active{
    background: #41B5F0!important;
    color: white!important;
  }
  .ivu-tabs-nav .ivu-tabs-tab{
    margin-right: 0!important;
  }
  .tec_name{
    font-size: 14px;
  }
  .tec_name span:first-child{
    margin-left: 15px;
  }
  .tec_name span:nth-child(2){
    margin-right: 15px;
  }
  .ivu-tabs-bar{
    margin-bottom:0!important;
  }
</style>



